@font-face {
  font-family: 'MaxterBoardSt';
  src: url(/react_turing_machine/static/media/MaxterBoardSt.5e31aada.ttf)  format('truetype'), /* Safari, Android, iOS */
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: left;
  font-size:16px;
}

p {
	font-size: 24px;
}

hr {
  border: 1px solid gray !important;
}

table {
  text-align: center;
}

tr:nth-child(odd) {
    background-color: #D4D3DA
}
td {
  border: 1px solid #7D7E7E;
}

th {
  background: #6B0803;
  color: white;
}

.paragraph {
	font-size: 24px;
	margin-bottom: 24px;
}

.clearfix {
	display: block;
	clear: both;
}

.hidden {
	display: none;
}

.center {
	text-align: center;
}

.App {
	border-top: 10px solid black;
}

.block-quote {
	border: 2px solid gray;
	padding:1em;
	margin: 1em 0;
}

.gothic {
	font-family: 'UnifrakturCook'
}

.emphasized {
	color:red;
	font-size:18px;
}

.machine-base {
	margin-left:20px;
	border-top: none;
	width: 10em;
	height: 360px;
}


.tape {
	position: absolute;
	top: 30%;
	border: 1px solid brown;
	border-right:none;
	border-left: none;
	width: 100%;
	height: 2em;
	background: white;
}

.display-tape {
	border: 1px solid brown;
	width: 52%;
	margin:10px auto;
	overflow: hidden;
	height: 34px;
}

.display-tape .scanned {
	background: orange;
}

.tape div.blank-tape, .display-tape div.blank-tape {
	width: 12em;
	height: 31px;
}

.tape div, .display-tape div {
	text-align:center;
	float:left;
	border-right: 2px dotted brown;
	height:31px;
	width: 33px;
}


.powerButton {
	cursor: pointer;
	border: 1px solid black;
	padding:10px;
	margin-top:50px;
	margin-left:25px;
	float:left;
	width:100px;
}

.powerButton img {
	display: block;
	margin:auto;
	width:50px;
}

.basicOperations {
	float:right;
	text-align: left;
	margin-right:24px;
}

.machine-container {
	position:relative;
	width: 100%;
	overflow:hidden;
}

footer {
	height:100px;
	background: red;
}

.footnote {
	position: relative;
  z-index: 5;
  top: -.1em;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  display: inline-block;
  padding: .35em;
  padding-right:3px;
  margin-right:10px;
  margin-left:0px;
  border: none;
  border-radius: .3em;
  cursor: pointer;
  background-color: #C8CDD5;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  font-size: 1rem;
  line-height: 0;
  vertical-align: middle;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  transition-property: background-color;
  transition-duration: .25s;
}

.footnote:hover {
	background:#790009;
}

.circle {
  display: inline-block;
  width: .25em;
  height: .25em;
  margin-right: .25em;
  float: left;
  color:red;
}
.tooltip-theme {
	max-width:500px;
	border-radius: 10px !important;
	font-size: 20px !important;
}

.tooltip-theme ul {
	padding:10px 20px 0;
	list-style: none;
	margin:0px;
}

div.log h2 {
	font-size:18px;
}

div.log p {
	font-size:14px;
}

div.log .display-tape {
	margin: 0px;
	height:25px;
	width: 100%;
}

div.log .display-tape div.blank-tape {
	width: 12em;
	height: 10px;
}

